import React from 'react'
import { useDispatch } from 'react-redux'
import { Modal } from 'antd'
import { PropTypes } from 'prop-types'
import { FormBuilder } from 'rc-form-pure'

import { saveNewObjectAction } from '../../store/slices/gtpSlice'
import { Button } from '../ui/Button'

import { CalibrationSettings } from './controls/CalibrationSettings'
import { Indicators } from './controls/Indicators'
import { Name } from './controls/Name'
import { OremSettings } from './controls/OremSettings'
import { RreSettings } from './controls/RreSettings'
import { VoltageLevels } from './controls/VoltageLevels'

export const AddObject = ({ isVisable, onModelClick }) => {
  const dispatch = useDispatch()

  const handleClick = () => {
    onModelClick()
  }
  const handleSubmit = (values, errors) => {
    if (!errors) {
      const newObject = {}
      newObject.Name = values.Name
      newObject.SupplierId = values.SupplierId
      newObject.PowerCategory = 'HIGH'
      newObject.PriceCategory = values.PriceCategory
      newObject.TransferPriceTable = { AVERAGE_VOLTAGE_2: values.voltage, HIGH_VOLTAGE: values.tariff }
      newObject.ProfileList = []
      dispatch(saveNewObjectAction(newObject))
    }
  }
  return (
    <Modal title="Add New Object" visible={isVisable} onCancel={handleClick} footer={null} width={500}>
      <FormBuilder withForm onSubmit={handleSubmit}>
        <Name />
        <RreSettings />
        <VoltageLevels />
        <OremSettings />
        <CalibrationSettings />
        <Indicators />
        <div>
          <Button block type="primary" htmlType="submit" size="large">
            Сохранить
          </Button>
        </div>
      </FormBuilder>
    </Modal>
  )
}

AddObject.propTypes = {
  onModelClick: PropTypes.func.isRequired,
  isVisable: PropTypes.bool,
}
