import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'

import analyticsInfo from '../../../mocks/data.json'
import { loadChart } from '../../../store/slices/chartSlice/chartSlice'
import { chartDataSelector } from '../../../selectors/chart'
import { gtpListSelector, gtpSelector } from '../../../selectors/gtp'

import { Chart } from './Chart'
import { ChartSidebar } from './ChartSidebar'
import styles from './styles.module.less'

const timeSeriesRows = analyticsInfo.TimeSeriesRows

export const ChartContainer = () => {
  const dispatch = useDispatch()
  const list = useSelector(gtpListSelector)
  const selectedGtp = useSelector(gtpSelector)
  const state = useSelector(chartDataSelector)

  useEffect(() => {
    // Get the first gtp by default
    const firstGtp = list?.[0]

    if (firstGtp && !selectedGtp.Id) {
      dispatch(loadChart({ id: firstGtp.Id }))
    }
  }, [dispatch, list, selectedGtp.Id])

  return (
    <div className={styles.container}>
      <ChartSidebar timeSeriesRows={timeSeriesRows} />
      <Chart timeSeriesRows={state} />
    </div>
  )
}
